@import "./src/assets/styles/variebles.scss";
@import "./src/assets/styles/mixins.scss";

.modalDelete {
  @extend %modal;

  z-index: 99;
}

.modalTop {
  @include flex();
  padding: 0 25px 15px;

  border-bottom: 1px solid #EDF0F6;
}

.caption {
  margin: 0;

  @extend %loginCaption;
  font-size: 18px;
}

.btnCross {
  @extend %cross;
  background-image: url(./../../../../assets/images/cross.svg);

}

.inputWrap {
  margin: 30px 20px 30px 25px;
}

.text {
  @extend %sf400;
  font-size: 14px;
  line-height: 1.6;
}

.accent {
  @extend %accentText;
}

.btns {
  margin: 0 25px;

  @include flex();

  padding-bottom: 25px;

  @include media(xs) {
    flex-direction: column-reverse;
  }
}

.button {
  max-width: 125px;

  @include media(xs) {
    max-width: 125px;
    margin-bottom: 30px;
  }
}

.button:first-child {
  @extend %btnCancel;
}

.button:last-child {
  @extend %btnDelete;
}




