@import "./src/assets/styles/variebles.scss";
@import "./src/assets/styles/mixins.scss";

.buttonSmall {
  padding: 7px;
  margin: 0 2px;

  border: none;
  border-radius: 2px;

  @extend %sf600;
  font-size: 12px;
  line-height: 1.05;
  text-transform: capitalize;

  cursor: pointer;

  &:hover {
    box-shadow: 0 1px 2px rgb(33, 38, 143);
  }

  &:active {
    box-shadow: 0 2px 4px rgb(33, 38, 143);
  }
}

.buttonSmallDisabled {
  padding: 7px;
  margin: 0 2px;

  border: none;
  border-radius: 2px;

  background-color: #efefef;
  color: #c1c1c1;
  @extend %sf600;
  font-size: 12px;
  line-height: 1.05;
  text-transform: capitalize;

  cursor: not-allowed;
}
