@import "./src/assets/styles/variebles.scss";
@import "./src/assets/styles/mixins.scss";

.cardsList {
  width: 100%;
}

.container {
  @extend %container;
  position: relative;
}

.inner {
  padding: 20px 25px 25px;

  min-height: 635px;

  background-color: #FEFEFF;
  border-radius: $br;
}

.topWrap {
  @include flex(row, center, flex-start);
  margin-bottom: 20px;
}

.btn {
  display: block;

  margin-right: 10px;

  width: 24px;
  height: 24px;

  background: transparent url("../../assets/images/arrow.svg") no-repeat;
  border: none;
  cursor: pointer;
}


.tableTitle {
  @extend %ff600;
  font-size: 22px;
  color: $textColor;
}

.searchWrap {
  @include flex();
}

.empty {
  position: absolute;
  top: 50%;
  right: 50%;

  transform: translate(50%, -50%);

  @extend %sf400;
  font-size: 16px;
  line-height: 1.3;
  color: #000000;
  opacity: 0.5;

   @media (max-width: 800px) {
     text-align: center;
   }
}

.button {
  margin: 0 0 0 25px;
}